import React from 'react'
import get from 'lodash/get'
import Header from '../../components/main-wrapper'
import { BlogSection, BlogContainer, Image, Bg, HeaderTitle,
  FeatureSection, SmallFeatureContainer, SmallFeature, SmallFeatureImage, CategoryContainer, CategoryText } from '../../pages/_ui'
  import { Link, BlogTitle, RouterBlogTitle, DateText, PostText } from '../../_style'
import { Wrap, BannerWithImage } from '../../pages/blog/_ui'
import Button from '../../components/button'
import { allCategories } from '../../pages'
import { PrevNextContainer } from './_ui'
import { graphql } from 'gatsby'

const NavLink = props => {
  if (!props.test) {
    return (
      <Link to={`/blog/${props.url}`} style={{ marginTop: 20, display: 'block' }}>
        <Button value={props.text} type="outlineDark" />
      </Link>
    )
  } else {
    return <span />
    return <span style={{ fontFamily: 'Josefin Sans', fontSize: 10, textTransform: 'uppercase', color: '#bbb' }}>{props.text}</span>;
  }
};

export default class BlogIndex extends React.Component {
  render() {
    const { group, index, first, last, pageCount } = this.props.pageContext;
    const previousUrl = index - 1 == 1 ? "" : (index - 1).toString();
    const nextUrl = (index + 1).toString();

    return (
      <Header {...this.props}>
        <BannerWithImage />
        <Wrap>
          <Bg />
          <BlogSection>
            {group.map(({ node }, i) => {
              const title = get(node, 'frontmatter.title') || node.fields.slug
              const imagePath = get(node, 'frontmatter.image.relativePath')
              return (
                <BlogContainer key={node.fields.slug}>
                  <div style={{ display: 'flex' }}>
                    <div>
                      <CategoryContainer>
                        {node.frontmatter.categories && node.frontmatter.categories.map(c => <CategoryText key={c}>{allCategories[c]}</CategoryText>)}
                      </CategoryContainer>
                      <RouterBlogTitle to={node.fields.slug}>
                        {title}
                      </RouterBlogTitle>
                      <DateText>{node.frontmatter.date}</DateText>
                      <PostText dangerouslySetInnerHTML={{ __html: node.excerpt }} onMouseOver={() => this.setState({ showImage: true })} />
                    </div>

                  </div>

                  <Link to={node.fields.slug} style={{ marginTop: 20, display: 'block' }}>
                    <Button value="Read More" type="teal" />
                  </Link>
                </BlogContainer>
              )
            })}
            <PrevNextContainer>
              <NavLink test={first} url={previousUrl} text="Previous" />
              <NavLink test={last} url={nextUrl} text="Next" />
            </PrevNextContainer>
          </BlogSection>
        </Wrap>


      </Header>
    )
  }
}

export const blogQuery = graphql`
  query BlogPageQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            categories
            date(formatString: "MMMM DD, YYYY")
            title
            image {
              relativePath
            }
          }
        }
      }
    }
  }
`
